import { Component, OnInit } from '@angular/core';
import {Route, Location } from './models';
import { RouteService } from './route.service';
import { Observable, of} from 'rxjs';
import { mapTo, delay, flatMap, concatAll, concatMap } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'route-app';

  ROUTES_URL = "http://127.0.0.1:8000/routes"

  // Flag indicating drawing line mode
  drawingMode = false;
     
  // Markers for drawing the line
  markers = [];
  route_lines = [];
  user_line = null;

  timeout_ids = [];

  route_names:any;
  routes:any;

  selectedRouteId:number = null;
  selectedRoutePoints: any = [];

  routePointsArr = [];

  lng: number = 34.7730261824675;
  lat: number = 32.0759398737466;
  zoom:number = 15;

  constructor(private routeService: RouteService) {
  }

  ngOnInit() {
    this.routeService.loadRoutes().subscribe((data) => {
      console.log(data);
      this.route_names = data;
    });
  }

  onMapClick(event: any) {
    if (this.drawingMode) {
      console.log("Clicked", event);
      this.markers.push(event.coords);  

      console.log(this.markers);
      if (this.markers.length > 1) {
        console.log(this.markers.length);
        this.routeService.intersections(this.markers).subscribe(routeIds => {
          console.log("Route ids", routeIds);

          this.markers = []; // clear line markers
          routeIds = Array.prototype.slice.call(routeIds);
          if(routeIds.length > 0) {
            for(var i = 0; i < routeIds.length; i++) {
              this.routeService.loadRoute(routeIds[i]).subscribe(routePoints => {
                this.routes.push(routePoints);
              });
            }
          }
        });
      }
    }
  }

  onSwitchDrawMode() {
    if (this.drawingMode) {
      // Clear user markers and line
      this.markers = [];
    }
    else{
      // Clear all drawn routes
    }
    this.selectedRoutePoints = [];
    this.drawingMode = !this.drawingMode;
  }

  onRouteSelected(route_id) {
    console.log("Selected route:", route_id, this.selectedRouteId);
    this.routeService.loadRoute(this.selectedRouteId).subscribe(routePoints => {
        this.routes = [];
        this.routes.push(Array.prototype.slice.call(routePoints));

        of(this.routes[0]).pipe(concatAll()) // unpack the array into single emissions
          .pipe(concatMap(val => of(val).pipe(delay(10)))) // delay each emission
          .subscribe(point => {
            this.selectedRoutePoints.push(point);
          });

        this.lat = routePoints[0].latitude;
        this.lng = routePoints[0].longitude;
        this.zoom = 15;

      
    })
  }

}
